import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/view/Home'


Vue.use(Router)



export default new Router({
  routes: [
    {
      path: '/',
      name: 'Home',
      component: Home
    },
    {
      path:'/tag/:tag',
      name:"Tag",
      component:Home
    }
  ]
})



